body {
    /* prevengono la scomparsa della scrollbar causata da Popover */
    overflow: auto !important;
    padding: 0 !important;
}

.body--hidden-scrollbar {
    overflow: hidden !important;
}

.MuiToolbar-root {
    padding-left: 16px !important
}

.MuiDialog-paperFullScreen {
    width: 95% !important;
    height: 95% !important;
}

.a11y input:focus, .a11y textarea:focus, .a11y a:focus, .a11y button:focus {
    background-color: #91A7C7 !important;
    border: 1px solid #00295a !important;
}

#skip-link {
    margin: 0;
}

#skip-link a, #skip-link a:visited {
    display: block;
    width: 100%;
    padding: 2px 0 3px 0;
    text-align: center;
    background-color: rgba(44, 44, 44, 0.5);
    color: #fff;
}

.element_invisible {
    position: fixed !important;
    clip: rect(1px 1px 1px 1px);
    /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
    overflow: hidden;
    height: 1px;
    z-index: -1;
    top: -10000px;
}

.element_invisible.element_focusable:active,
.element_invisible.element_focusable:focus {
    position: fixed !important;
    clip: auto;
    overflow: visible;
    height: auto;
    z-index: 99999;
    top: 0;
}